import { NgxSpinnerService } from "ngx-spinner";
import { EducationalService } from "app/admin/services/Admin/educational.service";
import { Component, OnInit } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { ActivatedRoute } from "@angular/router";
import { SubjectsService } from "app/admin/services/Admin/subjects.service";
import {
  country,
  stageByCountry,
  YearsByStage,
} from "app/shared/models/general/general";
import { GeneralService } from "app/shared/services/General/general.service";
import { ToastrService } from "ngx-toastr";
import { CustomeValidator } from "app/shared/validators/customeValid.validator";
import { environment } from "environments/environment";
import { ISubjectBodyVM } from "app/admin/models/admin/educations";
import { TranslateService } from "@ngx-translate/core";

@Component({
  selector: "app-add-subject",
  templateUrl: "./add-subject.component.html",
  styleUrls: ["./add-subject.component.scss"],
})
export class AddSubjectComponent implements OnInit {
  subjectId: number = 0;
  functionId: number;
  mainSubjects: any[] = [];
  countriesList: country[] = [];
  stagesList: stageByCountry[] = [];
  Years: YearsByStage[] = [];
  myForm!: FormGroup;
  submitted = false;
  coverPath = null; //"https://via.placeholder.com/150";
  currentLang: String;

  constructor(
    private route: ActivatedRoute,
    private fb: FormBuilder,
    private toastr: ToastrService,
    private translate: TranslateService,
    private SubjectsService: SubjectsService,
    private generlServ: GeneralService,
    private spinner: NgxSpinnerService,
    private eduServ: EducationalService
  ) {
    this.route.paramMap.subscribe((param) => {
      this.subjectId = Number(param.get("subjectId"));
      if (this.subjectId > 0) this.getSubjectByID(this.subjectId);
    });

    // current language
    this.translate.onLangChange.subscribe((lang) => {
      this.currentLang = lang.lang;
    });

    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
    });
  }

  ngOnInit(): void {
    this.getAllCountriesList();
    this.getMainSubject();

    this.myForm = this.fb.group({
      subject_id: [this.subjectId],
      subject_ar_name: [
        null,
        [
          Validators.required,
          Validators.maxLength(100),
          CustomeValidator.whiteSpace,
        ],
      ],
      subject_en_name: [
        null,
        [
          Validators.required,
          Validators.maxLength(100),
          CustomeValidator.whiteSpace,
        ],
      ],
      subjectThumbnailId: [null],
      parent_ID: [null],
      country_id: [null],
      stage_id: [null],
      Educational_yearID: [null, Validators.required],
      subjectPromoVideoPath: [null],
      subjectDescription: [null],
      // subjectDate: [null],
      // subjectEndDate: [null],
      subjectThumbnailFileBase64: [null],
    });
  }

  get FormCtrl() {
    return this.myForm.controls;
  }

  // get Subject By ID
  getSubjectByID(id: any) {
    this.spinner.show();
    this.SubjectsService.getSubjectByID(id).subscribe((res: any) => {
      console.log(res);
      if (res.subject_id != null) {
        this.subjectId = res.subject_id;
        this.myForm.patchValue(res);
        this.coverPath = `${environment.apiURL.replace("/api/", "")}${
          res.attach_path
        }`;
        this.getEducationalStageByCountryID();
        this.getYearbyStageID(res.stage_id);
        this.spinner.hide();
      }
    });
  }

  // Start: add Subject
  addEditSubject() {
    this.submitted = true;
    let body = {} as ISubjectBodyVM;
    body.subject_id = this.FormCtrl.subject_id?.value;
    body.subject_ar_name = this.FormCtrl.subject_ar_name?.value;
    body.subject_en_name = this.FormCtrl.subject_en_name?.value;
    body.parent_ID = this.FormCtrl.parent_ID?.value;
    body.Educational_yearID = this.FormCtrl.Educational_yearID?.value;
    body.subjectThumbnailId = this.FormCtrl.subjectThumbnailId?.value;
    body.subjectDescription = this.FormCtrl.subjectDescription?.value;
    body.subjectThumbnailFileBase64 =
      this.FormCtrl.subjectThumbnailFileBase64?.value;
    body.subjectPromoVideoPath = this.FormCtrl.subjectPromoVideoPath?.value;

    console.log("body", body);
    if (this.myForm.valid) {
      this.spinner.show();
      this.SubjectsService.addEditSubject(body).subscribe((res: any) => {
        console.log("SubjectAdded:", res);
        if (res.returnValue == 1) {
          this.submitted = false;
          this.spinner.hide();
          this.toastr.success("Subject Added successfully");
        }
      });
    }
  }

  //get Main Subject
  getMainSubject() {
    this.SubjectsService.getMainSubject().subscribe((res: any) => {
      this.mainSubjects = res;
      this.spinner.hide();
    });
  }

  //Get All Countries
  getAllCountriesList() {
    this.generlServ.getAllCountriesList().subscribe((res: country[]) => {
      this.countriesList = res;
      if (res.length == 1) {
        this.FormCtrl.country_id.setValue(res[0].country_id);
        this.getEducationalStageByCountryID();
      }
      // console.log(res);
      this.spinner.hide();
    });
  }

  //Get Stage By CountryId
  getEducationalStageByCountryID() {
    if (this.FormCtrl.country_id?.value) {
      this.spinner.show();
      this.eduServ
        .getEducationalStageByCountryID(this.FormCtrl.country_id?.value)
        .subscribe((res: any) => {
          this.stagesList = res.eduYearsList;
          if (this.stagesList?.length == 1) {
            this.FormCtrl.stage_id.setValue(this.stagesList[0]?.id);
            this.getYearbyStageID(this.stagesList[0]?.id);
          }
          this.spinner.hide();
        });
    }
  }

  //Get Years By StageId
  getYearbyStageID(StageId: any) {
    if (StageId != "") {
      this.spinner.show();
      this.eduServ.getYearbyStageID(StageId).subscribe((res: any) => {
        this.Years = res.eduYearsList;
        // console.log(res);
        this.spinner.hide();
      });
    }
  }

  imageUrl() {
    if (this.FormCtrl.subjectThumbnailFileBase64?.value?.FileAsBase64 != null)
      return this.FormCtrl.subjectThumbnailFileBase64?.value?.FileAsBase64;
    else return this.coverPath;
  }

  onChange(event: any) {
    let fileName = <File>event.target.files[0].name;
    let fileSize = <File>event.target.files[0].size;
    let fileType = <File>event.target.files[0].type;
    let LastModified = <File>event.target.files[0].lastModified;
    let LastModifiedDate = <File>event.target.files[0].lastModifiedDate;

    if (event.target.files) {
      var reader = new FileReader();
      reader.readAsDataURL(event.target.files[0]);
      reader.onload = (event: any) => {
        let fileReder = event.target.result;
        let data = {
          FileAsBase64: fileReder,
          name: fileName,
          size: fileSize,
          type: fileType,
          LastModified: LastModified,
          LastModifiedDate: LastModifiedDate,
        };
        this.FormCtrl.subjectThumbnailFileBase64?.setValue(data);
      };
    }
  }
}
